/* Set custom or change third-party Less variables */
/***************************************************/
/* Third party */
/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
These "retina group" variables are mappings for the naming and pairing of normal and retina sprites.

The list formatted variables are intended for mixins like `retina-sprite` and `retina-sprites`.
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}
*/
/*
The `retina-sprite` class sets up rules and a media query for a sprite/retina sprite.
  It should be used with a "retina group" variable.

The media query is from CSS Tricks: https://css-tricks.com/snippets/css/retina-display-media-query/

@icon-home-group: 'icon-home' @icon-home @icon-home-2x;

.icon-home {
  .retina-sprite(@icon-home-group);
}
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
/*
The `.retina-sprites` mixin generates a CSS rule and media query for retina groups
  This yields the same output as CSS retina template but can be overridden in LESS

.retina-sprites(@retina-groups);
*/
.svg-sprite--html5 {
  background-image: url("../img/sprites/svg/svg-sprite.svg");
  background-position: 0px 0px;
  width: 47px;
  height: 47px;
  background-repeat: no-repeat;
  background-size: 47px 47px;
}
.no-svg .svg-sprite--html5 {
  background-image: url("../img/sprites/svg/svg-sprite.png");
}
.png-sprite:before {
  content: '';
  display: inline-block;
}
.png-sprite--html5:before {
  background-image: url(../img/sprites/png/png-sprite.png);
  background-position: 0px 0px;
  width: 32px;
  height: 32px;
}
.png-sprite--html5-retina:before {
  background-image: url(../img/sprites/png/png-sprite.png);
  background-position: 0px 0px;
  width: 32px;
  height: 32px;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .png-sprite--html5-retina:before {
    background-image: url(../img/sprites/png/png-sprite-2x.png);
    background-size: 32px 32px;
  }
}
.svg-sprite:before {
  content: '';
  display: inline-block;
  width: 32px!important;
  height: 32px!important;
  background-size: contain!important;
}
.svg-sprite--html5-icon:before {
  background-image: url("../img/sprites/svg/svg-sprite.svg");
  background-position: 0px 0px;
  width: 47px;
  height: 47px;
  background-repeat: no-repeat;
  background-size: 47px 47px;
}
.no-svg .svg-sprite--html5-icon:before {
  background-image: url("../img/sprites/svg/svg-sprite.png");
}
.prettyprint {
  white-space: inherit;
}
.prettyprint ol li {
  list-style-type: inherit;
}
.navbar .navbar-nav > li > iframe {
  margin-top: 20px;
  margin-left: 5px;
}
.navbar .navbar-nav > li > .social-likes {
  margin-top: 15px!important;
  margin-left: 15px!important;
}
html,
body {
  margin: 0;
  padding: 0;
  background-color: #fafafa;
}
body {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #343434;
  letter-spacing: 1px;
}
body * {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-style: italic;
  font-size: 25px;
  margin: 0;
  text-transform: uppercase;
}
h2 {
  font-size: 35px;
  line-height: 48px;
  color: #ffffff;
}
h3 {
  margin: 40px 0;
  color: #d40000;
}
h4 {
  color: #d40000;
  font-size: 14px;
}
a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  color: #d40000;
}
.url {
  font-size: 14px;
  padding-top: 20px;
  cursor: pointer;
  height: 60px;
  background-color: #ffffff;
}
.mbr-container {
  width: 100%;
  max-width: 540px;
  margin: auto;
  background-color: #fff;
}
.mbr-slide {
  position: relative;
  height: 430px;
  text-align: center;
}
.mbr-slide--fullheight {
  height: 100vh;
  max-height: 667px;
}
.bg-banner {
  width: 100%;
  height: calc(100% - 90px);
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #fff;
}
.centered {
  display: table;
  text-align: center;
}
.centered > * {
  display: table-cell;
  vertical-align: middle;
}
.white {
  color: #ffffff;
}
.white * {
  color: #ffffff;
}
.logo {
  height: 90px;
  background-color: #d40000;
}
.logo img {
  height: 100%;
}
.desc {
  font-weight: 300;
  font-size: 20px;
  padding: 0 30px;
}
hr {
  margin: 40px auto;
  width: 94px;
}
form {
  width: 200px;
  margin: auto;
}
input,
select,
#vacancy-button {
  width: 100%;
  font-size: 15px;
  border: 0;
  box-shadow: 0;
  border-bottom: 1px solid #343434;
  padding-bottom: 10px;
  margin-bottom: 40px;
  background-color: transparent;
  border-radius: 0;
}
input:focus,
select:focus,
#vacancy-button:focus {
  outline: none;
  box-shadow: 0;
}
input.invalid,
select.invalid,
#vacancy-button.invalid {
  border-bottom: 2px solid #d40000;
}
::-webkit-input-placeholder {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #343434;
  letter-spacing: 1px;
}
#vacancy-button {
  padding: 0;
  padding-bottom: 10px;
}
#vacancy-button .ui-icon {
  position: relative;
  top: 2px;
  background-image: url(../img/svg/arrow.svg);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  transform: rotate(180deg);
  transform-origin: 50% 50%;
}
#vacancy-button[aria-expanded="true"] .ui-icon {
  transform: rotate(0deg);
}
#vacancy-menu li {
  border: 0;
  border-bottom: 1px solid #343434;
  padding: 12px 10px;
  font-size: 14px;
}
#vacancy-menu li:first-child {
  display: none;
}
button {
  width: 100%;
  height: 46px;
  text-transform: uppercase;
  font-weight: 700;
  color: #ffffff;
  background-color: #a60a10;
  border: 0;
  border-radius: 0;
  font-size: 15px;
  letter-spacing: 1px;
  margin-bottom: 70px;
  cursor: pointer;
}
button:focus {
  outline: none;
  box-shadow: 0;
}
.ui-selectmenu-open {
  width: 200px;
}
